.horizontal-data-grid {
 display: grid;
  grid-template-columns: repeat(3, minmax(100px, 1fr)); /* Fixed 3 columns */
  background-color: white;
  border-radius: 12px;
  gap: 15px;
  min-height: 10px;
  width: 99%;
  margin: 10px auto;
  padding: 10px 20px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
  font-size: 14px;
}

.horizontal-data-grid.small {
  grid-template-columns: repeat(auto-fit, minmax(70px, 1fr)); /* Reduce min-width for smaller screens */
  gap: 10px; /* Smaller gap */
  padding: 5px 10px; /* Reduce padding */
  font-size: 10px; /* Smaller font */
  height: 50px;
}

.banner-item {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px; /* Adjust padding */
  flex: 1; /* Allows it to grow */
  min-height: 10px; /* Ensures visibility */
}

.banner-key {
  font-size: 12px; /* Smaller font for keys */
  line-height: 1.2; /* Reduce line height */
  margin-bottom: 5px; /* Small gap between key and value */
}

.banner-value {
  font-size: 14px;
  font-weight: bold;
}

.horizontal-data-grid.small .banner-key {
  font-size: 8px;
  margin-bottom: 3px;
}

.horizontal-data-grid.small .banner-value {
  font-size: 10px;
}
  
  /* Responsive styles for small screens */
  @media screen and (max-width: 900px) {
    .horizontal-data-grid {
      grid-template-columns: repeat(2, 1fr); /* Force 2-column layout for small screens */
      gap: 10px; /* Reduce gap for smaller screens */
    }
  }
  