.referrer-panel-grid {
    display: grid;
    grid-template-columns: 80px 1fr;
    grid-template-rows: auto auto auto;
    grid-template-areas:
        "avatar info"
        "avatar info"
        "social social"
        "edit edit";
    gap: 0.5rem;
    padding: 1rem;
    background: white;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.15);
    max-width: 400px;
    min-width: 300px;
}
.referrer-panel-vertical{
  margin: 10px;
  margin-top: 20px;
}
.referrer-panel-head{
  display: flex;
  gap: 30px;
  margin: 5px auto;
}
.avatar-col {
    grid-area: avatar;
    display: flex;
    align-items: center;
    justify-content: center;
}

.avatar-img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    object-fit: cover;
}

.info-col {
    grid-area: info;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.profile-line {
    margin: 4px 0;
    display: flex;
    flex-direction: column;
}

.profile-line input, .testit {
    margin-top: 4px;
    padding: 4px;
    font-size: 0.9rem;
}

.social-row {
    width: 40%;
    grid-area: social;
    display: flex;
    justify-content: space-between;
    padding-top: 0.5rem;
    font-size: 1.5rem;
}
.social-row img {
    width: 40;
    height: 40;
    margin-right: 8px;
    border: 4px solid transparent;
  }

.social-row img:hover {
    filter: brightness(0.8);
    transform: scale(1.1);
    transition: filter 0.2s, transform 0.2s;
    transition: scale 0.2s, filter 0.2s;
    border: 2px solid gold;
    background-color: yellow;
}
.edit-btn-row {
    grid-area: edit;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 0.5rem;
}
.referrer-btn{
    width: 300px;
}
.edit-icon {
    position: absolute;
    top: 0px;
    right: 5px;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1rem;
    border: 2px solid transparent;
}

.edit-icon:hover{
  filter: brightness(0.8);
    transform: scale(1.1);
    transition: filter 0.2s, transform 0.2s;
    transition: scale 0.2s, filter 0.2s;
    border: 2px solid gold;
    background-color: yellow;
}
.copy-refcode {
  cursor: pointer;
  margin-left: 8px;
  display: inline-flex;
  align-items: center;
  user-select: all;
  transition: background 0.2s, border 0.2s;
  border-radius: 4px;
  padding: 2px 6px;
  border: 2px solid transparent;
}

.copy-refcode:hover {
  background: #f0f8ff;
  border: 2px solid #2196f3;
}

.copy-refcode.disabled {
  cursor: not-allowed;
  opacity: 0.5;
  pointer-events: none;
}

.refcode-value {
    background-color: white;
  margin-right: 6px;
  font-family: monospace;
  font-size: 1rem;
}

.copy-status {
  transition: opacity 0.3s;
  color: #2196f3;
  font-weight: 500;
  margin-left: 4px;
}

.copy-status.copied {
  color: #4caf50;
  font-weight: bold;
  animation: fadeOut 1s forwards;
}

@keyframes fadeOut {
  0% { opacity: 1; }
  80% { opacity: 1; }
  100% { opacity: 0; }
}
