/* src/components/VideoPlayer.css */
.video-container {
    width: 400px;
    overflow: hidden; /* Prevent overflow and scrollbars */
  }

  video {
    width: 100%;
    display: block; /* Ensure the video takes the full width of the container */
  }

  /* VideoPlayer.css */
.tv-frame {
  display: inline-block;
  padding: 20px;
  background: #222; /* TV body color */
  border-radius: 15px; /* Outer rounded corners */
  border: 8px solid #444; /* TV bezel */
  box-shadow:
    0 0 10px rgba(0,0,0,0.5),
    inset 0 0 20px rgba(0,0,0,0.8);
  position: relative;
}

.tv-frame::before {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border: 2px solid rgba(255,255,255,0.1);
  border-radius: 8px;
  pointer-events: none;
}

.video-element {
  border-radius: 5px; /* Inner video corners */
  border: 2px solid #000;
  display: block;
}


/*retro */
/* VideoPlayer.css *//* VideoPlayer.css */
.retro-tv-container {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.retro-tv {
  position: relative;
  width: 100%;
  max-width: 800px; /* Maximum size of the TV */
}

/* Antennas */
.antenna {
  position: absolute;
  width: 0.5%;
  height: 15%;
  max-height: 100px;
  background: #777;
  top: -15%;
}

.antenna.left {
  left: 15%;
  transform: rotate(-10deg);
}

.antenna.right {
  right: 15%;
  transform: rotate(10deg);
}

.antenna::after {
  content: '';
  position: absolute;
  top: 0;
  left: -200%;
  width: 500%;
  height: 30%;
  border-radius: 50%;
  background: #888;
}

/* TV Screen */
.tv-screen {
  background: #333;
  padding: 2%;
  border-radius: 2% 2% 0 0;
  border: 1.5% solid #555;
  box-shadow: inset 0 0 3% #000;
  position: relative;
  overflow: hidden;
}

.video-wrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
  height: 0;
  overflow: hidden;
  background: #000;
}

.video-wrapper video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid #000;
}

/* TV Stand */
.tv-stand {
  width: 8%;
  height: 4%;
  background: #555;
  margin: 0 auto;
}

.tv-base {
  width: 25%;
  height: 2%;
  background: #666;
  margin: 0 auto;
  border-radius: 0 0 1% 1%;
}

/* CRT Scan Lines Effect (optional) */
.tv-screen::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    to bottom,
    rgba(18, 16, 16, 0.1) 50%,
    transparent 50%
  );
  background-size: 100% 4px;
  pointer-events: none;
  z-index: 2;
}

/* Glow Effect (optional) */
.tv-screen::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(
    circle at center,
    rgba(100, 200, 255, 0.1) 0%,
    transparent 70%
  );
  z-index: 1;
  animation: pulse 6s infinite alternate;
}

@keyframes pulse {
  0% { opacity: 0.3; }
  100% { opacity: 0.7; }
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .antenna {
    display: none; /* Hide antennas on very small screens */
  }

  .tv-screen {
    border-width: 3%;
    padding: 3%;
  }
}
