/* Minimalist staking card */
.staking-card {
  background: #fff;
  border-radius: 14px;
  border: 1.5px solid #eee;
  max-width: 540px;
  margin: 2.5rem auto 1.5rem auto;
  padding: 1.5rem 1.2rem;
}

/* Simple header */
.staking-card h2 {
  color: #121212;
  font-size: 1.3rem;
  font-weight: 600;
  text-align: center;
  margin-bottom: 1.1rem;
  letter-spacing: 0.01em;
}

/* Tabs */
.staking-tabs {
  display: flex;
  justify-content: center;
  gap: 1.1rem;
  margin-bottom: 1.3rem;
}
.staking-tabs button {
  background: none;
  color: #0099f1;
  border: none;
  border-bottom: 2px solid transparent;
  font-size: 1rem;
  font-weight: 500;
  padding: 0.6rem 1.2rem 0.5rem 1.2rem;
  cursor: pointer;
  border-radius: 0;
  transition: border 0.18s, color 0.18s;
}
.staking-tabs button.active {
  border-bottom: 2px solid #0099f1;
  color: #333;
  background: #f8fcff;
}

/* Rewards summary */
.staking-rewards-box {
  background: #f8fcff;
  border-radius: 8px;
  border: 1px solid #e0e8f7;
  padding: 1rem 0.7rem;
  margin-bottom: 1.2rem;
  text-align: center;
}
.staking-rewards-box .reward-label {
  font-size: 1rem;
  color: #0099f1;
  margin-bottom: 0.2rem;
  font-weight: 600;
}
.staking-rewards-box .reward-value {
  font-size: 1.1rem;
  color: #222;
  font-weight: 600;
}

/* NFT grid and item */
.nft-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(96px, 1fr));
  gap: 0.7rem;
  margin-bottom: 1rem;
}
.nft-item {
  background: #fff;
  border: 1.2px solid #e6e8ee;
  border-radius: 7px;
  transition: 0.12s;
  text-align: center;
  cursor: pointer;
  padding: 0.45rem 0.2rem;
  min-height: 90px;
}
.nft-grid >.ticket:hover:not(.selected) {
  border: 1.2px solid grey;
}
.nft-grid > .selected {
  border: 2.5px solid yellow;
  background: yellow;
}


/* Minimalist primary buttons */
.stake-button,
.unstake-button {
  display: block;
  width: 100%;
  border: none;
  border-radius: 9px;
  font-size: 1.07rem;
  font-weight: 500;
  padding: 0.7rem 0;
  color: #fff;
  margin: 1rem 0 0.2rem 0;
  cursor: pointer;
  background: #0099f1;
  transition: background 0.14s, color 0.14s;
}
.stake-button:disabled,
.unstake-button:disabled {
  background: #b8e7fa;
  color: #888;
  cursor: not-allowed;
}
.unstake-button {
  background: #00d49f;
  color: #fff;
}

/* Penalty/Warnings */
.penalty-warning {
  color: #e35252;
  font-size: 0.95rem;
  text-align: center;
  margin-top: 0.45rem;
  font-weight: 500;
}

/* Staking container */
.staking-container {
  width: 90%;
  margin: 2em auto;
  padding: 1.5rem;
  background: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
.staking-container > h2,h3 {
  color: #000; /* Sets the text color to black */
}

/* Rewards card container */
.rewards-card {
  /* background: #fff; */
  /* border-radius: 12px; */
  /* border: 1px solid #e0e0e0; */
  padding: 1.5rem;
  max-width: 70%;
  margin: 10px auto;
  /* box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); */
  text-align: center;
}

/* Rewards card header */
.rewards-card h3 {
  font-size: 1.2rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 1rem;
}

/* Rewards info container */
.rewards-info {
  margin-bottom: 1.5rem;
}

/* Individual rewards row */
.rewards-row {
  display: flex;
  justify-content: space-between;
  font-size: 1rem;
  color: #555;
  margin-bottom: 0.8rem;
}

/* Claim button */
.claim-button {
  background: #0099f1;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 0.7rem 1.2rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}

.claim-button:disabled {
  background: #b8e7fa;
  color: #888;
  cursor: not-allowed;
}

.claim-button:hover:not(:disabled) {
  background: #007acc;
}

@media (max-width: 800px) {
  .staking-card {
    padding: 0.8rem 0.5rem;
  }
  .staking-tabs button {
    font-size: 0.9rem;
    padding: 0.4rem 0.6rem;
  }
  .nft-grid {
    grid-template-columns: 1fr;
    gap: 0.7rem;
    max-width: 90%; 
    margin: auto; 

  }
  .nft-grid > .ticket > .holders-logo,.ticket-inner {
    max-width: 450px;
    width: 100%;;
  }
  .nft-grid > .ticket {
    width: 100%;
    min-width: 50px !important;
    max-width: 450px !important;
    box-sizing: border-box;
  }
  .stake-button,
  .unstake-button,
  .claim-button {
    font-size: 0.9rem;
    padding: 0.6rem;
  }
  .staking-container {
    width: 100%;
    margin: 15px 0px;
    padding: 1rem;
  }
  .nftPage{
    padding: 15px;
  }
  .rewards-card{
    padding: 15px;
    margin: 0px;
    min-width: 100% !important;
  }
  .rewards-row{
    font-size:0.8rem;
  }
}

