.popup-container {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    color: #000;
    border: 2px solid #141212;
    outline: none;
    transition: 0.4s;
    border-radius: 10px;
  }
  
  .popup-content {
    background-color: white;
    border-radius: 5px;
    position: relative;
  }

  .region-alert{
    background-color: palevioletred;
    color: black;
    position: relative;
    top: 50em;
  }
  
  .flash-border {
    border-color: red;
    animation: flash-border-animation 0.5s ease-in-out;
  }
  
  @keyframes flash-border-animation {
    0% { border-color: red; }
    50% { border-color: yellow; }
    100% { border-color: red; }
  }
  
  .error-controls {
    display: flex;
    justify-content: space-between;
    align-items: center; /* Align buttons vertically */
    width: 100%; /* Ensure full width */
    margin-top: 2px;
    padding: 5px 0;
  }
  
  .error-controls button {
    flex: 1; /* Make buttons evenly sized */
    border: none;
    background: transparent; /* No solid background */
    color: #333; /* Darker text for contrast */
    font-size: 10px;
    font-weight: 100;
    text-transform: uppercase; /* Subtle uppercase for a sleek look */
    letter-spacing: 0.5px;
    cursor: pointer;
    transition: color 0.2s ease-in-out, opacity 0.2s;
  }
  
  .error-controls button:not(:disabled):hover {
    color: #000; /* Darker color on hover */
    text-decoration: underline; /* Subtle hover effect */
  }
  
  .error-controls button:disabled {
    opacity: 0.4; /* Make disabled buttons subtle */
    cursor: default;
  }

  /* Minimized State */
.minimized {
  position: fixed;
  bottom: 2em;
  right: 02em;
  z-index: 1000;
  max-height: 30px;
  opacity: 0.8;
  pointer-events: auto;
}

.minimized-badge {
  background: grey;
  color: white;
  padding: 6px 12px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.minimized-badge:hover {
  transform: scale(1.1);
}

/* Error Actions (Minimize & Close) */
.error-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.minimize-btn,
.close-btn {
  padding: 5px 10px;
  background: transparent;
  border: none;
  color: #333;
  font-size: 14px;
  cursor: pointer;
  transition: opacity 0.2s;
}

.minimize-btn:hover,
.close-btn:hover {
  opacity: 0.7;
}