  .wallet {
    margin: 0px -10px 10px -5px;
  }

  .wallet-button {
    position: relative;
    width: 9em;
    height: 45px;
    transform: scale(0.8);
    font-size: 17px;
    font-weight: bold;
    border-radius: 25px !important;
    border: 3px white solid;
    box-shadow: none;
    background-color: rgba(200, 137, 206, 0.733);
    overflow-y: hidden;
  }
  .wallet-button:hover{
    background-color: rgba(177, 12, 192, 0.733);
    border: 3px grey solid;
  }
  .screenBlock{
    all: initial;
    position:absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: 30;
  }

  .vhead{
