/* =========================
   Table Header Styling
========================= */
table.dataTable thead th.sorting,
table.dataTable thead th.sorting_asc,
table.dataTable thead th.sorting_desc {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding-right: 0;
}
table.dataTable thead th {
  text-align: center;
  padding-right: 0;
}

/* =========================
   Static Page Layout
========================= */
.media-inner {
  padding-top: 10em;
}
.highscore-head {
  color: black;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 600;
}
.highScore_header {
  padding-top: 5px;
  margin-top: 15px;
  width: 75%;
}

/* =========================
   Table Container Box
========================= */
#referal-table {
  position: relative;
  width: 100% !important;
  margin: 0 auto !important;
  overflow: auto;
}


/* =========================
   Top Controls Layout Swap
========================= */
.dt-layout-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  margin: 10px 0;
}

/* Swap positions */
.dt-layout-row:nth-of-type(1) .dt-layout-start {
  order: 2; /* entries dropdown */
  flex: 0 1 30%;
  display: flex;
  justify-content: flex-end;
}
.dt-layout-row:nth-of-type(1) .dt-layout-end {
  order: 1; /* search bar */
  flex: 1 1 65%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.dt-layout-row:nth-of-type(3) .dt-layout-start {
  order: 2; /* entries dropdown */
  flex: 0 1 35%;
  display: flex;
  justify-content: flex-end;
}
.dt-layout-row:nth-of-type(3) .dt-layout-end {
  order: 1; /* search bar */
  flex: 1 1 65%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

/* Search Box Styling */
.dt-search {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 0 20px;
}
.dt-search > label {
  white-space: nowrap;
  font-weight: 500;
  width: 10px;
}

.dt-search > input[type="search"] {
  flex: 1;
  max-width: 350px;
  padding: 10px 14px;
  font-size: 14px;
  border: 1px solid #ccc;
  background-color: white;
  outline: none;
  transition: all 0.3s ease;
}
.dt-search > input[type="search"]:hover {
  border-color: #888;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);
}
.dt-search > input[type="search"]:focus {
  border-color: #4a90e2;
  box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.2);
}

/* Entries Dropdown */
.dt-length {
  display: flex;
  align-items: center;
  gap: 10px;
}
.dt-length > label {
  display: flex;
  align-items: center;
  gap: 8px;
  white-space: nowrap;
}
.dt-length select {
  padding: 6px 10px;
  border-radius: 6px;
  font-size: 14px;
  border: 1px solid #ccc;
  margin-top: 0;
}

/* =========================
   Footer: Pagination + Info
========================= */
.dt-layout-row.dt-layout-footer {
  flex-direction: column;
  align-items: stretch;
  gap: 10px;
  margin-top: 20px;
}

/* Pagination Stretch */
.dt-paging {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 6px;
}
.dt-paging-button {
  min-width: 55px;
  padding: 10px 18px;
  font-size: 18px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: white;
  text-align: center;
  transition: background-color 0.3s ease;
}
.dt-paging-button:hover {
  background-color: #f5f5f5;
  border-color: #888;
}
.dt-paging-button.current {
  background-color: #4a90e2;
  color: white;
  border-color: #4a90e2;
}

/* Info Text Under Pagination */
#referal-table_info {
  align-self: flex-end;
  text-align: right;
  font-size: 13px;
  color: #444;
  margin-right: 8px;
}

.table-container {
  width: 100%;
  max-width: 100vw;
  font-family: 'Poppins', sans-serif;
  min-width: 0;
}

/* =========================
   Wrapper Overflow Fix
========================= */
#referal-table_wrapper {
  overflow: auto;

}

/* Tab Navigation Styles */
.tab-container{
  margin: 2em auto !important;
  padding: 24px;
}

.tab-buttons {
  display: flex;
  gap: 0;
  flex-wrap: wrap;
  margin-bottom: -1px;
  z-index: 10;
  position: relative;
  color:red;
}
.tab-content {
  width: 100%;
  box-shadow: 0 4px 24px var(--color-box-shadow);
  position: relative;
  z-index: 9;
}


/*DataBar*/
.data-bar {
  display: flex;
  gap: 25px;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 2em auto;
  width: 100%;;
}

.data-card {
  border-radius: 8px;
  flex: 1 1 0;
  min-width: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px;
  justify-content: center;
  box-shadow: 0 0 8px var(--color-box-shadow);
}

/*Styling for UserData overview*/
.bar-icon img {
  width: 24px;
  height: 24px;
  margin-bottom: 5px;
}

.bar-value {
  font-size: 20px;
  font-weight: bold;
  color: var(--color-text-light);
}

.bar-key {
  font-size: 14px;
  color: var(--color-text-muted-2);
  margin-top: 4px;
}


/*styling for reward overview*/
/* Tier badge (e.g., Bronze, Silver, Gold, Platinum) */
.bar-tier {
  background-color: var(--color-secondary);
  display: inline-block;
  padding: 6px 14px;
  border-radius: 16px;
  font-weight: bold;
  margin-bottom: 12px;
  color: var(--color-glass-bg); /*var(--color-text-light);*/
  font-size: 0.9rem;
}

/* Referrals Required */
.bar-referrals_required {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 6px;
}

/* Commission Rate */
.bar-commission_rate {
  color: var(--color-primary);
  font-size: 1.1rem;
  font-weight: bold;
  margin-top: 12px;
  margin-bottom: 4px;
}

/* Tier Bonus */
.bar-tier_bonus {
  color: var(--color-secondary);
  font-size: 1.2rem;
  font-weight: bold;
  margin-top: 8px;
}
